        <el-button v-else @click="openDialog(true)" class="btn" icon="el-icon-user">添加联系人信息</el-button>
      </el-form-item>
      <el-form-item label="养老院价格信息">
        <el-card v-if="home.priceDetails" class="box-card card" shadow="hover">
          <div slot="header" class="clearfix">
            <span>总费用：{{ totalCost }}</span>
            <el-button @click="deleteCard(false)" style="float: right;margin-left: 5px" size="medium" type="danger"
                       icon="el-icon-delete" circle/>
            <el-button @click="editCard(false)" style="float: right" size="medium" type="primary" icon="el-icon-edit"
                       circle/>
          </div>
          <el-row v-if="price.bedFee">
            <el-col :span="7">床位费用：</el-col>
