          <el-row v-if="price.otherFee">
            <el-col :span="7">其他费用：</el-col>
            <el-col :span="17">{{ price.otherFee }}</el-col>
          </el-row>
        </el-card>
        <el-button v-else @click="openDialog(false)" class="btn" icon="el-icon-coin">添加养老院价格信息</el-button>
      </el-form-item>
      <el-form-item label="特色服务">
        <el-input
            type="textarea"
            v-model="home.specialServices"
            style="width: 40%"
            clearable
            :rows="3"/>
      </el-form-item>
      <el-form-item style="margin-top: 50px">
        <el-button @click="addCareHome" class="btn" icon="el-icon-finished">保存并添加</el-button>
        <el-button @click="clear" type="warning" icon="el-icon-delete" class="btnWithoutColor">清空表单</el-button>
        <el-button @click="$router.push('/care-home')" type="danger" icon="el-icon-back" class="btnWithoutColor danger">返回
        </el-button>
