                 :append-to-body="false"
                 @close="closeDialog(true)">
        <h2 align="center" style="color: var(--deepText);padding-top: 20px">{{ btnWord }}联系人信息</h2>
        <el-divider/>
        <div style="margin: 5% 10% 3%;padding: 3% 5% 1%">
          <el-form :model="contact" label-width="100px" label-position="left">
            <el-form-item style="margin-top: -30px" label="联系人姓名">
              <el-input
                  v-model="contact.name"
                  clearable
                  style="width: 70%"
                  maxlength="10"
                  show-word-limit
