                  maxlength="32"
                  show-word-limit
              />
            </el-form-item>
            <el-form-item label="微信账号">
              <el-input
                  v-model="contact.wechatId"
                  clearable
                  style="width: 100%"
                  maxlength="32"
                  show-word-limit
              />
            </el-form-item>
            <el-form-item label="微博账号">
              <el-input
                  v-model="contact.weiboId"
                  clearable
                  style="width: 100%"
                  maxlength="32"
