          <el-button @click="addContact" class="formBtn">{{ btnWord }}</el-button>
          <el-button @click="closeDialog(true)" class="formBtn">取消</el-button>
        </div>
      </el-dialog>
      <!--第二个弹窗-->
      <el-dialog :visible.sync="dialog2"
                 :show-close="false"
                 :limit="1"
                 :modal-append-to-body='false'
                 :append-to-body="false"
                 @close="closeDialog(false)">
        <h2 align="center" style="color: var(--deepText);padding-top: 20px">{{ btnWord }}价格信息</h2>
        <el-divider/>
        <div style="margin: 5% 10% 3%;padding: 3% 5% 1%">
          <el-form style="margin-bottom: 70px" :model="price" label-width="150px" label-position="left">
            <el-form-item label="养老院名称">
              <span>{{ home.name }}</span>
            </el-form-item>
            <el-form-item style="margin-top: 30px" label="总费用">
              <span style="color: var(--lightText)">{{ totalCost }}</span>
            </el-form-item>
            <el-form-item style="margin-top: 30px" label="床位费用 (月)">
              <el-input-number :min="0" style="width: 150px" v-model="price.bedFee" :step="100"></el-input-number>
            </el-form-item>
            <el-form-item style="margin-top: 30px" label="护理费用 (月)">
              <el-input-number :min="0" style="width: 150px" v-model="price.nursingFee" :step="100"></el-input-number>
            </el-form-item>
            <el-form-item style="margin-top: 30px" label="餐饮费用 (月)">
              <el-input-number :min="0" style="width: 150px" v-model="price.foodFee" :step="100"></el-input-number>
            </el-form-item>
            <el-form-item style="margin-top: 30px" label="其他费用 (月)">
              <el-input-number :min="0" style="width: 150px" v-model="price.otherFee" :step="100"></el-input-number>
            </el-form-item>
          </el-form>
        </div>
        <el-divider/>
        <div style="display: flex;flex-direction: row">
          <el-button @click="addPrice" class="formBtn">{{ btnWord }}</el-button>
          <el-button @click="closeDialog(true)" class="formBtn">取消</el-button>
        </div>
      </el-dialog>
    </el-form>
  </div>
</template>

<script>


import {serverIp} from "../../../../public/config";

export default {
  name: "CareHomeData",
  data() {
    return {
      //显示的养老院信息
      home: {},
      contact: {},
      price: {},
      //add,edit,show 分别是新增、修改、展示信息
      mode: 'add',
      //本机地址
      serverIp: serverIp,
      //临时显示的图片的本地地址
      upload1: '',
      upload2: '',
      //弹窗显示
      dialog1: false,
      dialog2: false,
      //弹窗按钮文字
      btnWord: '添加'
    }
  },
  computed: {
    //计算总费用
    totalCost() {
      let bed = this.price.bedFee ? this.price.bedFee : 0
      let nursing = this.price.nursingFee ? this.price.nursingFee : 0
      let food = this.price.foodFee ? this.price.foodFee : 0
      let other = this.price.otherFee ? this.price.otherFee : 0
      return bed + nursing + food + other + ' 元/月'
    }
  },
  methods: {
    //显示上传的封面
    uploadChange(file, b) {
      const reader = new FileReader() // 创建FileReader对象
      reader.readAsDataURL(file.raw) // 读取选择的文件的数据
      reader.onload = () => {
        if (b) {
          this.upload1 = reader.result; // 将文件的本地路径保存到data属性中
        } else {
          this.upload2 = reader.result; // 将文件的本地路径保存到data属性中
        }
      }
    },
    //上传封面
    handleUpload1(res) {
      if (res.code !== '200') {
        this.$notify({
          title: '展示图片上传失败',
          message: res.msg,
          type: 'error'
        })
      } else {
        //将返回的封面路径存入对象
        this.home.imageGallery = res.msg
      }
    },
    handleUpload2(res) {
      if (res.code !== '200') {
        this.$notify({
          title: '荣誉图上传失败',
          message: res.msg,
          type: 'error'
        })
      } else {
        //将返回的封面路径存入对象
        this.home.honors = res.msg
      }
    },
    //清空表单信息
    clear() {
      this.home = {}
      this.price = {}
      this.contact = {}
      this.upload1 = ''
      this.upload2 = ''
    },
    //打开弹窗
    openDialog(b) {
      if (b) {
        this.dialog1 = true
      } else {
        this.dialog2 = true
      }
      this.btnWord = '添加'
    },
    //关闭弹窗
    closeDialog(b) {
      if (this.btnWord === '添加') {
        if (b) {
          if (!this.home.contactsId) {
            this.contact = {}
          }
        } else {
          if (!this.home.priceDetails) {
            this.price = {}
          }
        }
      }
      this.dialog1 = false
      this.dialog2 = false
    },
    //添加价格和联系方式
    addPrice() {
      if (!this.home.priceDetails) {
        this.home.priceDetails = '1'
      }
      this.dialog2 = false
    },
    addContact() {
      if (!this.home.contactsId) {
        this.home.contactsId = '1'
      }
      this.dialog1 = false
    },
    //编辑和删除操作
    editCard(b) {
      if (b) {
        this.dialog1 = true
      } else {
        this.dialog2 = true
      }
      this.btnWord = "修改"
    },
    deleteCard(b) {
      if (b) {
        this.home.contactsId = null
        this.contact = {}
      } else {
        this.home.priceDetails = null
        this.price = {}
      }
    },
    //添加养老院数据
    addCareHome() {
      //先提交图片
      if (this.upload1 !== '') {
        this.$refs.avatar1.submit()
      }
      if (this.upload2 !== '') {
        this.$refs.avatar2.submit()
      }
      //先保存养老院信息
      this.request.post('/details/add', this.home).then(res => {
        if (res.code === '200') {
          this.$notify({
            type: 'success',
            title: '保存成功',
            message: '养老院信息已保存'
          })
          //成功后将id分配给价格和联系方式
          this.contact.contactId = res.msg
          this.price.careHomeId = res.msg
          this.home.id = res.msg
          //再提交联系人信息和价格信息
          this.submitContactAndPrice()
        } else {
          this.$notify({
            type: 'error',
            title: '养老院信息保存失败',
            message: res.msg
          })
        }
      }).catch(err => {
        this.$notify({
          type: 'error',
          title: '养老院信息保存失败',
          message: err
        })
      })
    },
    //更新养老院信息
    updateDetails() {
      this.request.put('/details/edit', this.home).then(res => {
        if (res.code !== '200') {
          this.$notify({
            type: 'error',
            title: '养老院信息更新失败',
            message: res.msg
          })
        } else {
          //保存成功
          this.clear()
          this.$router.push('/care-home')
        }
      }).catch(err => {
        this.$notify({
          type: 'error',
          title: '养老院信息更新失败',
          message: err
        })
      })
    },
    //提交联系人和价格信息
    submitContactAndPrice() {
      //添加
      this.request.post('/contacts/add', this.contact).then(res => {
        if (res.code !== '200') {
          this.$notify({
            type: 'error',
            title: '联系人信息保存失败',
            message: res.msg
          })
        } else {
          this.home.contactsId = res.msg
          this.request.post('/price-details/add', this.price).then(res => {
            if (res.code !== '200') {
              this.$notify({
                type: 'error',
                title: '价格信息保存失败',
                message: res.msg
              })
            } else {
              this.home.priceDetails = res.msg
              this.updateDetails()
            }
          }).catch(err => {
            this.$notify({
              type: 'error',
              title: '价格信息保存失败',
              message: err
            })
          })
        }
      }).catch(err => {
        this.$notify({
          type: 'error',
          title: '联系人信息保存失败',
          message: err
        })
      })

      // //更新
      // this.request.put('/contacts/edit', this.contact).then(res => {
      //   if (res.code !== '200') {
      //     this.$notify({
      //       type: 'error',
      //       title: '联系人信息更新失败',
      //       message: res.msg
      //     })
      //   }
      // }).catch(err => {
      //   this.$notify({
      //     type: 'error',
      //     title: '联系人信息更新失败',
      //     message: err
      //   })
      // })
      // this.request.put('/price-details/edit', this.price).then(res => {
      //   if (res.code !== '200') {
      //     this.$notify({
      //       type: 'error',
      //       title: '价格信息更新失败',
      //       message: res.msg
      //     })
      //   }
      // }).catch(err => {
      //   this.$notify({
      //     type: 'error',
      //     title: '价格信息更新失败',
      //     message: err
      //   })
      // })
    }
  },
  mounted() {
    //获取传递的数据
    this.mode = this.$route.query.mode
  },

}
</script>

